body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-title {
  text-align: center;
  font-size: 22px;
  margin-bottom: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; }
  @media all and (min-width: 768px) {
    .app-title {
      font-size: 50px; } }

.app-filter {
  display: flex;
  justify-content: center; }

.characters__list {
  display: flex;
  max-width: 100vw;
  flex-wrap: wrap;
  margin-top: 50px;
  padding: 5px;
  justify-content: center; }

.characters__list-item {
  list-style-type: none;
  margin: 10px;
  padding: 0;
  border: 1px solid black;
  border-radius: 5px;
  background-color: orangered; }

.characters__list-item-link {
  text-decoration: none;
  color: white; }

.character-image {
  width: 163px;
  height: 227px; }

.character-name {
  font-size: 16px;
  text-align: center; }

.character-house {
  text-align: center;
  font-size: 12px; }

.detail-card {
  width: 50vw;
  margin: 0 auto;
  border: 1px solid black;
  background-color: orangered;
  border-radius: 5px;
  margin-bottom: 20px; }
  @media all and (min-width: 768px) {
    .detail-card {
      width: 500px;
      height: 250px;
      display: flex; } }

.detail-card__image {
  width: 100%;
  height: 230px; }
  @media all and (min-width: 768px) {
    .detail-card__image {
      width: 200px;
      height: 100%; } }

.detail-card__info {
  text-align: center;
  color: white; }
  @media all and (min-width: 768px) {
    .detail-card__info {
      padding-left: 20px;
      width: 100%;
      text-align: left; } }

@media all and (min-width: 768px) {
  .detail-card__name {
    text-align: center;
    margin-bottom: 50px; } }

.detail-card__house {
  margin: 0; }
  @media all and (min-width: 768px) {
    .detail-card__house {
      margin-bottom: 10px; } }

.detail-card__birth {
  margin: 0; }
  @media all and (min-width: 768px) {
    .detail-card__birth {
      margin-bottom: 10px; } }

.detail-card__patronus {
  margin: 0; }
  @media all and (min-width: 768px) {
    .detail-card__patronus {
      margin-bottom: 10px; } }

.detail-card__state {
  margin: 0;
  margin-bottom: 10px; }

.go-back {
  text-decoration: none;
  color: black;
  margin-left: 45%; }

